<div class="goods"><van-swipe class="goods-swipe" :autoplay="3000"><van-swipe-item v-for="thumb in goods.thumb" :key="thumb"><img :src="thumb"></van-swipe-item></van-swipe><van-cell-group><van-cell><div class="goods-title">{{ goods.title }}</div><div class="goods-price">{{ formatPrice(goods.price) }}</div></van-cell><div class="goods-express"><van-col span="10">运费：{{ goods.express }}</van-col><van-col span="14">剩余：{{ goods.remain }}</van-col></div></van-cell-group><van-cell-group class="goods-cell-group"><van-cell value="进入店铺" icon="shop-o" is-link @click="sorry"><template v-slot="title"><span class="van-cell-text">有赞的店</span><van-tag class="goods-tag" type="danger">官方</van-tag></template></van-cell><van-cell title="线下门店" icon="location-o" is-link @click="sorry"></van-cell></van-cell-group><van-cell-group class="goods-cell-group"><van-cell title="查看商品详情" is-link @click="sorry"></van-cell></van-cell-group><van-action-bar><van-action-bar-icon icon="chat-o" @click="sorry">客服</van-action-bar-icon><van-action-bar-icon icon="cart-o" @click="onClickCart">购物车</van-action-bar-icon><van-action-bar-button type="warning" @click="sorry">加入购物车</van-action-bar-button><van-action-bar-button type="danger" @click="sorry">立即购买</van-action-bar-button></van-action-bar></div>